<template lang="pug">
  component(:is="name" :fill="color").icon
</template>

<script>
import componentsFromFolder from '@/utilities/componentsFromFolder'

const context = require.context('@/assets/icons', false, /\.svg$/)

import ccsVariables from '@/style/variables.sass'

export default {
  name: 'icon',
  components: {
    ...componentsFromFolder(context),
  },

  props: {
    name: {type: String, required: true},
    color: {type: String, default: ''},
  },

  data: () => ({
    ccsVariables,
  }),
}
</script>
